.trip {
    margin: 4rem 6rem;
    text-align: center;
    color: #222;
}
.trip h1 {
    font-size: 3rem;
}
.tripCards {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}
.t-card {
   width: 32%;
   text-align: start;
   border-radius: 5px;
   box-shadow: 0 3px 25px 2px rgba(0, 0, 0, 0.4);
   cursor: pointer;
   padding: 1rem 0.8rem;
}
.t-image {
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}
.t-image img {
    height: 100%;
    width: 100%;
    transition: all 0.3s ease-in-out;
}
.t-image:hover img {
    transform: scale(1.2);
}
.t-card h3 {
    font-size: 1.3rem;
    padding: 0.9rem 0 0.2rem 0;
}
@media screen and (max-width: 850px) {
    .trip {
        margin: 4rem 2rem;
    }
    .tripCards {
        margin-top: 3rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .t-card {
        width: 100%;
        margin: 0;
     }
}