.NavbarItems {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    border-radius: 13px;
    background-color: #fff;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.3);
    margin-top: 5px;
    width: 90%;
    height: 80px;
    margin: 0 3%;
    position: absolute;
    z-index: 99;
}
.Navbar-logo {
    font-size: 2rem;
    color: #222;
    cursor: pointer;
}
.Navbar-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    list-style: none;
}
.Navbar-links {
    text-decoration: none;
    padding-right: 10px;
    color: #222;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 0.7rem 1.2rem;
    white-space: nowrap;
}
.Navbar-links:hover {
    background-color: rgb(255, 191, 0);
    color: #000;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
}
.Navbar-links-mobile {
    display: none;
}
button {
    background-color: orange;
    color: #222;
    border-radius: 5px;
    border: none;
    padding: 0.5rem 1rem;
    font-weight: 600;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
button:hover {
    background-color: rgb(255, 191, 0);
    color: #222;
}
.menu-icons {
    display: none;
}
@media screen and (max-width: 850px){
    .NavbarItems{
        z-index: 99;
        margin: auto;
    }
    .Navbar-menu {
        display: flex;
        flex-direction: column;
        background-color: white;
        justify-content: flex-start;
        text-align: center;
        width: 100%;
        height: auto;
        position: absolute;
        align-items: stretch;
        left: 100%;
        top: 0;
        opacity: 0;
        padding: 80px 0 30px 0;
        margin: 0;
        z-index: -1;
    }
    .Navbar-menu.active{
        left: 0%;
        opacity: 1;
        z-index: -1;
    }
    .Navbar-links {
        display: block;
        width: 100%;
        font-size: 1.2rem;
        padding: 2rem 0;
    }
    .Navbar-links:hover {
        background-color: rgb(255, 191, 0);
        color: #000;
        transition: all 0.2s ease-in-out;
        z-index: -1;
    }
    button {
        display: none;
    }
    .Navbar-links-mobile {
        display: block;
        padding-bottom: 1rem;
        text-align: center;
        margin: auto;
        border-radius: 5px;
        width: 80%;
        height: auto;
        text-decoration: none;
        font-weight: 800;
        font-size: 1.2rem;
        color: #222;
        background-color: rgb(255, 191, 0);
        border-radius: 5px;
    }
    .menu-icons {
        display: block;
        font-weight: 600;
        color: #222;
    }

}