.footer {
    padding: 4rem 6rem;
    background-color: black;
    color: #fff;
}
.upper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: start;
    flex-wrap: wrap;
}
.upper i {
    color: #fff;
    font-size: 1.3rem;
    margin-left: 0.5rem;
}
.upper i:hover {
    color: rgb(255, 191, 0);
}
.lower {
    padding-top: 2rem;
    text-align: start;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.lower div {
    display: flex;
    flex-direction: column;
}
.lower h1 {
    font-size: 1.2rem;
    padding: 1rem 0 0.5rem 0;
    text-align: start;
}
.lower a {
    text-decoration: none;
    color: #fff;
    font-weight: 300;
    font-size: 1.2rem;
    padding-bottom: 0.4rem;
}
.lower a:hover {
    color: rgb(255, 191, 0);
}