.hero {
    width: 100%;
    height: 100%;
    position: relative;
}
.hero-about, .hero-contact, .hero-service {
    width: 100vw;
    height: 70vh;
    position: relative;
}
img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.hero-text {
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 90%;
}
.hero-text h1 {
    font-size: 3rem;
    color: #fff;
    font-weight: 700;
}
.hero-text p {
    color: #fff;
    font-size: 1.5rem;
    font-weight: 500;
    padding: 0.5rem 0 1rem 0;
}
.hero-text .hero-btn {
    text-decoration: none;
    font-weight: 500;
    padding: 0.5rem 1rem;
    color: #222;
    background-color:rgb(255, 153, 0);
    border-radius: 5px;
    border: none;
    transition: all 0.3s ease-in-out;
}
.hero-btn:hover {
    background-color: rgb(255, 191, 0);
}

/*    About       */
.hero-about .hero-btn {
    text-decoration: none;
    font-weight: 500;
    padding: 0.5rem 1rem;
    color: #222;
    background-color:rgb(0, 187, 255);
    border-radius: 5px;
    border: none;
    transition: all 0.3s ease-in-out;
}
.hero-about .hero-btn:hover {
    background-color: rgb(0, 136, 255);
}






@media screen and (max-width: 555px){
    .hero-text h1 {
        padding-inline-start: 10px 20px;
    }
    .hero-text p {
        font-size: 1.5rem;
        padding: 0 0 2rem 0;
    }
    .hero-text .hero-btn {
        font-size: 1rem;
        padding: 0.6rem 1rem;
        border-radius: 5px;
    }
}