.destination {
    margin: 4rem 6rem;
    text-align: center;
}
.destination h1 {
    font-size: 3rem;
}
.first-des {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;
}
.first-des-reverse {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;
}
.des-text {
    width: 45%;
    text-align: center;
    font-size: 1.1rem;
}
.des-text h3 {
    padding-bottom: 1rem;
}
.image {
    position: relative;
    width: 50%;
    display: flex;
}
.image img{
    width: 49%;
    height: 350px;
    object-fit: cover;
    border-radius: 5px;
    box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.3);
}
.image img:nth-child(2) {
    position: absolute;
    top: -10%;
    right: 0;
}
@media screen and (max-width: 850px) {
    .destination {
        margin: 4rem 2rem;
    }
    .first-des {
        display: flex;
        flex-direction: column-reverse;
        margin-top: 2.5rem;
    }
    .first-des-reverse {
        display: flex;
        flex-direction: column-reverse;
        margin-top: 2.5rem;
    }
    .des-text {
        width: 100%;
    }
    .image {
        width: 100%;
        margin: 1.8rem 0;
    }
}